import React from 'react'

const Message = ({ message, success }) => {
    
    if (message === null) {
        return null
    } else {
        let style
        if (success) style = successStyle
        else style = errorStyle

        return (
            <div style={style}>
                {message}
            </div>
        )
    }
}

const errorStyle = {
    width: '50vw',
    border: 'solid 1px #A52A2A',
    backgroundColor: '#FFC0CB',
    marginBottom: '10px',
    padding: '10px'
}
const successStyle = {
    width: '50vw',
    border: 'solid 1px #006400',
    backgroundColor: '#98FB98',
    marginBottom: '10px',
    padding: '10px'
}

export default Message