import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom'
import PersonForm from './PersonForm';
import Persons from './Persons';
import Filter from './Filter';
import request from './services/httprequests'
import Message from './Message';
import { decode } from 'punycode';

const App = () => {
    const [newName, setNewName] = useState('')
    const [newNumber, setNewNumber] = useState('')
    const [searchResult, setSearchResult] = useState([])
    const [persons, setPersons] = useState([])
    const [msg, setMsg] = useState(null)
    const [success, setSuccess] = useState(null)

    useEffect(() => {
        request.read().then(x => {
            setPersons(x)
        })
    }, [])

    const handleChangeInput = ev => {
        if (ev.target.id === 'name') setNewName(ev.target.value)
        if (ev.target.id === 'number') setNewNumber(ev.target.value)
    }
    const handleSubmit = ev => {

        ev.preventDefault()
        const check = persons.find(x => x.name.toLowerCase() === newName.toLowerCase())

        if (typeof check !== 'undefined') {
            if (window.confirm(`${newName} is already added to phonebook, replace the old number with a new one?`)) {
                const update = { ...check, number: newNumber }
                request.update(update.id, update).then(res => {
                    setPersons(persons.map(person => person.id === update.id ? update : person))
                    createMessage(`${update.name} updated!`)
                    setSuccess(true)
                })
            }
        } else {
            const newPerson = {
                name: newName,
                number: newNumber
            }
            request.create(newPerson).then(p => {
                setPersons(persons.concat(p))
                createMessage(`Added ${newName}`)
                setSuccess(true)
            }).catch(err => {
                const errMsg = err.response.data.error ? err.response.data.error : err.response.data
                setMsg(errMsg)
                setSuccess(false)
            })
        }
        setNewName('')
        setNewNumber('')
    }
    const handleDelete = (ev) => {
        console.log('ev.target.id', ev.target.id)
        const id = ev.target.id
        const toDel = persons.find(x => x.id === id)
        console.log('toDel', toDel)
        console.log('persons', persons)
        if (window.confirm(`Delete ${toDel.name}?`)) {
            request.del(id)
                .then(() => {
                    setPersons(persons.filter(x => x.id !== toDel.id))
                    createMessage(`${toDel.name} deleted`)
                    setSuccess(true)
                })
                .catch(() => {
                    createMessage(`Info for ${toDel.name} has already been removed from server`)
                    setSuccess(false)

                })
        }
    }
    const handleSearchInput = ev => {

        if (ev.target.value !== '') {
            const term = "^" + ev.target.value
            const reg = new RegExp(term, 'i')
            const find = persons.filter(x => reg.test(x.name))
            setSearchResult(find)
        }
        else setSearchResult([])
    }

    const createMessage = (mess) => {
        setMsg(mess)
        setTimeout(() => {
            setMsg(null)
            setSuccess(null)
        }, 2000)
    }

    return (
        <div style={{ margin: "50px" }}>
            <h2>Phonebook</h2>
            <Message message={msg} success={success} />
            <Filter handleSearchInput={handleSearchInput} />
            <h3>Add a new</h3>
            <PersonForm
                handleChangeInput={handleChangeInput}
                handleSubmit={handleSubmit}
                newName={newName}
                newNumber={newNumber}
            />
            {searchResult.length === 0 ? <h3>All Numbers</h3> : <h3>Search results</h3>}
            <Persons persons={searchResult.length === 0 ? persons : searchResult} handleDelete={handleDelete} />
            <a href="/info/">info!</a>
        </div>
    )
}

ReactDOM.render(<App />, document.getElementById('root'))