import React from 'react'

const PersonForm = ({ handleChangeInput, handleSubmit, newName, newNumber }) => {


    return (
        <div>
            <form onSubmit={handleSubmit}>
                <div>
                    name: <input id="name" value={newName} onChange={handleChangeInput} />
                </div>
                <div>
                    number: <input id="number" value={newNumber} onChange={handleChangeInput} />
                </div>
                <div>
                    <br />
                    <button type="submit">add</button>
                </div>
            </form>

        </div>
    )
}


export default PersonForm